// 颜色值
$grey: (
  "grey":       #9e9e9e,
  "grey-l5":  #fafafa,
  "grey-l4":  #f5f5f5,
  "grey-l3":  #eeeeee,
  "grey-l2":  #e0e0e0,
  "grey-l1":  #bdbdbd,
  "grey-d1":   #757575,
  "grey-d2":   #616161,
  "grey-d3":   #424242,
  "grey-d4":   #212121
) !default;

$color-base:(
  'white': #fff,
  'black':#000,
  'transparent': transparent
) !default;

$color-info:#2196f3 !default;
$color-success:#4caf50 !default;;
$color-warning:#F7B824 !default;;
$color-error:#FF5722 !default;;
$color-primary:#5FB878 !default;;

$color-theme:(
  'primary': $color-primary,
  'info': $color-info,
  'success': $color-success,
  'error': $color-error,
  'warning': $color-warning
) !default;

$color-border:map_get($grey, 'grey-l2');
$color-bg:map_get($grey, 'grey-l4');
$color-hover:map_get($grey,'grey-l3');

// 块的基础高度
$gutter:8px;
$block-base-height: $gutter * 4;
$border-radius: 4px;

$font-size:20;
$font-family:"Microsoft YaHei UI";

// media
$grid-screen: (
  xs: 576,
  sm: 768,
  md: 992,
  lg: 1200
) !default;



