body {
  background-color: #f8f8f8;
}
a, body {
  color: #909090;
}
html, body, input, select, button {
  font-family: "Microsoft YaHei UI";
}
p {
  padding: 0;
  margin: 0;
  margin-bottom: 8px;
}
.inline-block {
  display: inline-block;
}
.top-nav {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 60px;
  line-height: 60px;
  background: url("../assets/top-nav_bg.gif") repeat-x;
  word-spacing: -6px;
  color: #fff;
  z-index: 889;
}
.top-nav_logo {
  display: inline-block;
  width: 250px;
  font-size: 24px;
  word-spacing: 0;
}
.top-nav_logo a {
    color: #fff;
}
.top-nav_user {
  display: inline-block;
  word-spacing: 0;
  height: 60px;
  line-height: 60px;
  min-width: 150px;
  cursor: pointer;
  background-color: transparent;
  transition: all 0.2s;
}
.left-nav {
  position: fixed;
  left: 0;
  top: 60px;
  width: 250px;
  height: 100%;
  background-color: #fff;
  z-index: 888;
}
.view-box {
  position: relative;
  margin-left: 250px;
  top: 60px;
}
.tree {
  font-size: 0.8rem;
}
.tree .tree-node {
    border-top: 1px solid transparent;
    border-bottom: 1px solid #f8f8f8;
    transition: all 0.2s;
}
.tree .tree-node.active.empty {
      background-color: #f8f8f8;
      border-top: 1px solid #ebebeb;
      border-bottom: 1px solid #ebebeb;
}
.tree .tree-node.active.empty .tree-node_label {
        color: #4dd1de;
}
.tree .tree-node_title {
    height: 56px;
    line-height: 56px;
    padding: 0 16px;
}
.crumbs {
  height: 56px;
  line-height: 56px;
  background-color: #f1f1f1;
  padding: 0 24px;
  margin-bottom: 16px;
}
.crumbs-item {
  position: relative;
  display: inline-block;
  padding-right: 8px;
}
.crumbs-item:after {
    content: ">";
    margin-left: 8px;
}
.crumbs-item:last-child:after {
    content: "";
}
.v-content {
  padding: 0 24px;
}
.btns-group {
  margin-bottom: 16px;
  min-height: 35px;
}
.btns-group .comb-btn {
    margin-right: 8px;
}
.comb-btn {
  box-shadow: none;
  width: 100px;
  height: 35px !important;
  line-height: 35px !important;
  border-radius: 20px !important;
  padding: 0;
  color: #fff;
}
.comb-msg .msg-content_head {
  color: #4dd1de;
  font-size: 0.8rem;
  font-size: 500;
}
.comb-msg .msg-content {
  max-width: 780px;
  padding: 8px 8px 0 8px;
  border-radius: 10px;
}
.comb-msg.mini .msg-content {
  max-width: 500px;
}
.comb-msg .msg-content_body {
  margin-bottom: 8px;
}
input, textarea {
  outline: none;
  border: none;
}
.form-control {
  display: block;
  width: 100%;
  padding: 16px;
  background-color: #fff;
  border-radius: 10px;
  border: 1px solid #ebebeb;
  transition: border-color ease-in-out .3s;
}
.form-control:focus {
