}
.tab-theme-default .tab-label.active {
  color: #4dd1de !important;
}
.tab-theme-default .tab-line {
  background-color: #4dd1de;
}
.tab-theme-block .tab-label {
  padding: 8px 16px !important;
  min-width: 80px;
  text-align: center;
}
.tab-theme-block .tab-label.active {
    background-color: #4dd1de !important;
}
.switch-item {
  display: inline-block;
  padding: 32px 56px;
  background-color: #fff;
  margin-right: 16px;
  border: 1px solid #ebebeb;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;
  margin-bottom: 36px;
}
.switch-item:after {
    position: absolute;
    content: "";
    left: 50%;
    bottom: -20px;
    width: 40px;
    height: 40px;
    margin-left: -20px;
    background: url("../assets/selected.png") no-repeat;
    -webkit-transform: scale(0, 0);
            transform: scale(0, 0);
    transition: all 0.2s;
    transition-delay: 0.1s;
}
.switch-item:hover, .switch-item.active {
    border-color: #4dd1de;
}
.switch-item:hover:after, .switch-item.active:after {
      -webkit-transform: scale(1, 1);
              transform: scale(1, 1);
}
.switch-item_img {
  padding: 0 48px;
  height: 90px;
  line-height: 90px;
  vertical-align: top;
}
.switch-item_img img {
    display: inline-block;
    vertical-align: middle;
}
.panel, .panel-tab {
  margin: 0 24px 16px 24px;
  padding: 24px;
  background-color: #fff;
  border-radius: 5px;
  border: 1px solid #ebebeb;
}
.panel-tab {
  padding: 0;
}
.panel-tab.no-mrg {
    margin: 0;
}
.panel-tab.only-mrg-b {
    margin: 0;
    margin-bottom: 24px;
}
.panel-tab .tab-label-group {
    padding: 16px 24px;
    border-bottom: 1px solid #ebebeb;
}
.panel-tab .tab-cont {
    padding: 16px 24px;
}
.info-panel {
  position: relative;
}
.info-panel > .btns-group {
    position: absolute;
    right: 24px;
    top: 24px;
}
.info-left {
  display: table-cell;
}
.info-icon {
  width: 190px;
  height: 190px;
  text-align: center;
  border: 5px solid #4dd1de;
  color: #4dd1de;
  border-radius: 10px;
  font-size: 3rem;
}
.info-right {
  display: table-cell;
  vertical-align: top;
  padding-left: 16px;
  width: 100%;
}
.info-title {
  margin: 8px 0 16px 0;
  font-size: 1.28rem;
  color: #4dd1de;
  font-weight: 700;
}
.info-desc {
  margin-top: 24px;
}
.piece-search {
