  border-radius: 50px;
  padding: 0 16px;
  background-color: #ebebeb;
}
.piece-search .icon {
    font-size: 1rem;
    cursor: pointer;
    vertical-align: middle;
}
.piece-search_input {
  min-width: 200px;
  display: inline-block;
  background-color: transparent;
}
.el-editor {
  border: 1px solid transparent;
  padding: 8px 0 !important;
  width: 100%;
  border-radius: 5px;
}
.el-editor[contenteditable='true'] {
  border-color: #ebebeb;
  padding: 8px 8px !important;
}
.el-editor[contenteditable='true']:focus {
    border-color: #4dd1de;
}
.detail-title {
  font-size: 1.1rem !important;
}
.select.active .select-label, .select:hover .select-label {
  border-color: #4dd1de !important;
}
.list-item.active {
  background-color: #4dd1de !important;
}
.comb-label {
  padding: 2px 4px;
  border-radius: 5px;
}



