
.select {
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.select.active .select-label {
    border-color: #5FB878;
}
.select.active .caret {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.select:not([disabled]):hover .select-label {
    border-color: #5FB878;
}
.select-label {
  position: relative;
  padding: 8px 16px;
  border: 1px solid #e0e0e0;
  transition: all 0.2s;
  cursor: pointer;
  background-color: #fff;
  border-radius: 4px;
}
.select-label::after {
    display: block;
    content: "";
    clear: both;
}
.select-label_angle {
  margin-top: -2px;
}
.list.select-list {
  border-top-right-radius: 0;
  border-top-left-radius: 0;
}



