/*  基础样式
   ========================================================================== */

html {
  height: 100%;
  box-sizing: border-box;

  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
  font-size: 20px;
}

body {
  min-height: 100%;
  margin: 0;
  position: relative;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-size: 0.7rem;
  font-weight: 400;
}



/*  初始化浏览器样式
   ========================================================================== */

ul:not(.browser) {
  padding: 0;margin: 0;
}
li:not(.browser)  {
  list-style: none;
}

small {
  font-size: 0.6rem;
}

// h1~h6
$element-h:(
  h1:1.8rem,
  h2:1.5rem,
  h3:1.2rem,
  h4:0.9rem,
  h5:0.7rem,
  h6:0.6rem,
) !default;
@each $key,$value in $element-h {
  #{$key}:not(.browser) {
    padding: 0;margin: 0;
    font-size: $value;
    font-weight: 500;
  }
}

button {
  outline: none;border: none;background: none;
}

a {
  text-decoration: none;
}

/*  初始化图像影音标签
   ========================================================================== */

audio,
img,
svg,
object,
embed,
canvas,
video,
iframe {
  max-width: 100%;
  font-style: italic;
  vertical-align: middle;
}



