
.btn {
  display: inline-block;
  text-align: center;
  border-radius: 4px;
  padding: 0 8px;
  cursor: pointer;
}
.btn[disabled] {
    cursor: no-drop;
}
.btn.hover {
    box-shadow: none;
    transition: .2s ease-out;
}
.btn.hover:hover {
      box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);
}



