
.msg {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all 0.2s;
}
.msg-overflow {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.msg-content {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 90%;
  border-radius: 5px;
  background-color: #fff;
}
.msg-content_head {
  height: 48px;
  line-height: 48px;
  padding: 0 16px;
}
.msg-content_body {
  padding: 8px 16px;
  min-height: 48px;
  vertical-align: middle;
}
.msg-content_foot {
  padding: 0 16px;
  margin-bottom: 16px;
  text-align: right;
}
.msg-content_foot > .btn {
    margin-left: 8px;
    min-width: 80px;
}
.msg-content_close {
  right: 16px;
  top: 14px;
  cursor: pointer;
  transition: all 0.2s;
}
.msg-content_close:hover {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}



