
.radio > input:checked + label:after {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}
.radio > label:before {
  border-radius: 50%;
}
.radio > label:after {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
  left: 4px;
  top: 7px;
  -webkit-transform: scale(0, 0);
          transform: scale(0, 0);
}



