$height: 20px;
.formbylabel {
  > input {
    position: absolute;
    left: -9999px;
    opacity: 0;
    &[type='radio'],&[type='checkbox'] {
      &:checked {
        + label {
          &:before {
            background-color: $color-primary;
            border-color: transparent;
          }
        }
      }
      + label {
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: $height * 0.8;
          height: 80%;
          border: 1px solid map_get($grey, 'grey-l1');
          background-color: #fff;
          transition: all 0.2s;
        }
        &:after {
          content: '';
          position: absolute;
          transition: .2s ease-out;
          transition-delay: 0.1s;
        }
      }
    }
  }
  > label {
    position: relative;
    height: $height;
    padding-left: $height * 0.8;
    cursor: pointer;
    display: inline-block;
    vertical-align: middle;
  }

  ._label {
    margin-left: 8px;
    white-space: nowrap;
    font-size: 14rem/$font-size;
  }
  &:not([disabled]):hover {
    > label:before {
      border-color: $color-primary;
    }
  }
}



