
.textarea-group {
  position: relative;
}
.textarea-group.active .textarea {
    border-color: #5FB878;
}
.textarea-group[disabled]:after {
    opacity: 0.5;
    background-color: #f5f5f5;
}
.textarea-group:not([disabled]):hover .textarea {
    border-color: #5FB878;
}
.textarea {
  display: inline-block;
  padding: 8px 16px;
  width: 100%;
  border: 1px solid #e0e0e0;
  outline: none;
  transition: all 0.2s;
  min-height: 100px;
  font-family: "Microsoft YaHei UI";
  border-radius: 4px;
}



