.formbylabel > input {
  position: absolute;
  left: -9999px;
  opacity: 0; }
  .formbylabel > input[type='radio']:checked + label:before, .formbylabel > input[type='checkbox']:checked + label:before {
    background-color: #5FB878;
    border-color: transparent; }
  .formbylabel > input[type='radio'] + label:before, .formbylabel > input[type='checkbox'] + label:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
