
.toast {
  padding: 4px 10px;
  background-color: #212121;
  color: #fff;
  border-radius: 5px;
  transition: opacity 0.2s;
}
.toast.info {
    background-color: #5FB878;
    color: #fff;
}
.toast.t {
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    top: 16px;
}
.toast.b {
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    bottom: 16px;
}
.toast.l {
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    left: 16px;
}
.toast.r {
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    right: 16px;
}
.toast.lt {
    left: 16px;
    top: 16px;
}
.toast.lb {
    left: 16px;
    bottom: 16px;
}
.toast.rt {
    right: 16px;
    top: 16px;
}
.toast.rb {
    right: 16px;
    bottom: 16px;
}
.toast.cc {
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}



