
.tab-label-group {
  position: relative;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.tab-label {
  cursor: pointer;
}
.tab-cont {
  width: 100%;
  padding: 16px;
}
.tab-cont_item {
  display: none;
}
.tab-cont_item.active {
    display: block;
}
.tab-line {
  position: absolute;
  content: "";
  bottom: -1px;
  left: 0;
  height: 2px;
  width: 1px;
  background-color: #5FB878;
  transition: width 0.2s, left 0.3s;
  padding: 0 !important;
}
.tab-theme-default .tab-label-group {
  border-bottom: 1px solid #eeeeee;
}
.tab-theme-default .tab-label {
  display: inline-block;
  padding: 8px;
  margin-right: 8px;
  font-weight: 500;
}
.tab-theme-default .tab-label.active {
    color: #5FB878;
}
.tab-theme-block .tab-label {
  display: inline-block;
  border: 1px solid #eeeeee;
  border-radius: 50px;
  padding: 4px 16px;
  margin-right: 8px;
  background-color: transparent;
  transition: all 0.2s;
}
.tab-theme-block .tab-label.active {
    background-color: #5FB878;
    color: #fff;
}



