
::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
::-webkit-outer-spin-button {
  -webkit-appearance: none;
}
input[type="number"] {
  -moz-appearance: textfield;
}
.input-group {
  position: relative;
}
.input-group.active .input, .input-group:not([disabled]):hover .input {
    border-color: #5FB878;
}
.input-group.active .input-label, .input-group:not([disabled]):hover .input-label {
    background-color: #5FB878;
    color: #fff;
}
.input-group[disabled]:after {
    opacity: 0.5;
    background-color: #f5f5f5;
}
.input {
  display: inline-block;
  padding: 0 16px;
  width: 100%;
  border: 1px solid #e0e0e0;
  outline: none;
  transition: all 0.2s;
  border-radius: 4px;
}
input[type='number'] {
  padding-right: 66px;
}
input[type='number'] + .input-number {
    display: block;
}
.input-number {
  display: none;
  position: absolute;
  top: 1px;
  right: 1px;
  height: calc(100% - 2px);
  word-spacing: -4px;
  overflow: hidden;
}
.input-number_button {
  padding: 6px 0;
  width: 25px;
  text-align: center;
  display: inline-block;
  word-spacing: 0;
  cursor: pointer;
  background-color: #e0e0e0;
  transition: all 0.2s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.input-number_button:hover {
    background-color: #5FB878;
    color: #fff;
}
.input-number_button:last-child {
    border-top-right-radius: 4px;
    border-top-bottom-radius: 4px;
}
.input-label {
  position: absolute;
  top: 1px;
  height: calc(100% - 2px);
  background-color: #e0e0e0;
  padding: 0 16px;
  transition: all 0.2s;
}
.input-label.pos-left {
    left: 1px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}
.input-label.pos-right {
    right: 1px;
}



