
.tree {
  padding: 0;
  margin: 0;
}
.tree::after {
    display: block;
    content: "";
    clear: both;
}
.tree-node {
  cursor: pointer;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.tree-node.active .caret {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.tree-node.active > .tree-node_child {
    max-height: 180px;
}
.tree-node_title {
  position: relative;
  padding: 8px 0;
}
.tree-node_label {
  display: inline-block;
  margin-left: 8px;
}
.tree-angle {
  float: right;
  margin-top: -2px;
}
.tree-node_child {
  max-height: 0;
  transition: max-height 0.2s;
}
.tree-node_link {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}



